<template>
  <section class="card">
    <div class="card-header">
      <div class="utils__title">
        <strong>Add Shipment Data</strong>
      </div>
    </div>
    <div class="card-body">
      <div :class="$style.addPost">
        <a-form class="mt-3" :form="form" @submit="handleSubmit">
          <div class="form-group">
            <a-form-item label="Shipping Mark">
              <a-select
                showSearch
                placeholder="Select a person"
                optionFilterProp="children"
                style="width: 100%"
                @focus="handleFocus"
                @blur="handleBlur"
                @change="handleChange"
                :filterOption="filterOption"
                v-decorator="['shipping',{ rules: [{ required: true, message: 'Please provide shipping mark' }]}]"
              >
                <a-select-option v-for="(user, index) in usersData" :key="index" :value="user.id">{{user.customer_name}}</a-select-option>
<!--                <a-select-option value="jack">Jack</a-select-option>-->
<!--                <a-select-option value="lucy">Lucy</a-select-option>-->
<!--                <a-select-option value="tom">Tom</a-select-option>-->
              </a-select>
            </a-form-item>
            <a-form-item label="Phone Number">
              <a-input-number
                :initialValue="0"
                v-decorator="['pn',{ rules: [{validator: validatePhone},{ required: true, message: 'Please provide a Phone Number' }]}]"
                placeholder="Phone Number"
                style="width: 100%"
              />
            </a-form-item>
            <a-form-item label="CTN">
              <a-input-number
                :initialValue="0"
                v-decorator="['ctn',{ rules: [{ required: true, message: 'Please provide a CTN Number' }]}]"
                placeholder="CTN Number"
                style="width: 100%"
              />
            </a-form-item>
            <a-form-item label="CBM">
              <a-input-number
                :initialValue="0"
                v-decorator="['cbm',{ rules: [{ required: true, message: 'Please provide a CBM Number' }]}]"
                placeholder="CBM Number"
                style="width: 100%"
              />
            </a-form-item>
            <a-form-item label="Total in USD">
              <a-input-number
                :initialValue="0"
                :formatter="value => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')"
                :parser="value => value.replace(/\$\s?|(,*)/g, '')"
                @blur="onChange"
                v-decorator="['usd',{ rules: [{ required: true, message: 'Please provide an Amount in USD' }]}]"
                placeholder="Total in USD"
                id="usd"
                style="width: 100%"
              />
            </a-form-item>
            <a-form-item :label=" false_conversion === false ? 'Total in Naira > to be auto-generated' : 'Total in Naira > Sorry conversion could not be established now, do convert manually' ">
              <a-input-number
                :initialValue="0"
                :formatter="value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')"
                :parser="value => value.replace(/\$\s?|(,*)/g, '')"
                v-decorator="['naira',{ rules: [{ required: true, message: 'Please provide an Amount in NGN' }]}]"
                placeholder="Total in Naira would be auto-generated"
                id="naira"
                 :disabled="false_conversion === false ? true : false "
                style="width: 100%"
              />
            </a-form-item>
            <a-form-item label="Container Number">
              <a-input-number
                :initialValue="0"
                v-decorator="['container_number',{ rules: [{ required: true, message: 'Please provide a container Number' }]}]"
                placeholder="Container Number"
                style="width: 100%"
              />
            </a-form-item>
            <br>
            <div class="form-group">
              <a-form-item>
                <div :class="$style.submit">
                  <span class="mr-3">
                    <a-button type="primary" htmlType="submit">Create Shipment</a-button>
                  </span>
                  <a-button @click="handleReset()" type="danger">Discard</a-button>
                </div>
              </a-form-item>
            </div>
          </div>
        </a-form>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'create',
  data() {
    return {
      form: this.$form.createForm(this),
      converted: false,
      usersData: [],
      converted_value: 4444,
      false_conversion: false,
    }
  },
  mounted() {
    this.getAllCustomers()
  },
  methods: {
    handleChange(value) {
      // console.log(`selected ${value}`)
    },
    handleBlur() {
      // console.log('blur')
    },
    handleFocus() {
      // console.log('focus')
    },
    filterOption(input, option) {
      return (
        option.componentOptions.children[0].text.toLowerCase().indexOf(input.toLowerCase()) >= 0
      )
    },
    onChange() {
      const usdValue = document.getElementById('usd').value
      this.axios.get('convert_to_naira/' + usdValue)
        .then(response => {
          // console.log(response.data.data.value)
          this.converted = true
          this.handleConversionData(response.data.data.value)
        })
        .catch(error => {
          console.log(error)
        })
    },
    handleConversionData(e) {
      if (e === 0) {
        this.false_conversion = true
      } else {
        this.form.setFieldsValue({
          naira: e,
        })
      }
    },
    convertAmount(amount, fromCurrency, toCurrency, cb) {
      var apiKey = this.$store.state.conversionApiKey

      fromCurrency = encodeURIComponent(fromCurrency)
      toCurrency = encodeURIComponent(toCurrency)
      var query = fromCurrency + '_' + toCurrency

      var url = 'https://api.currconv.com/api/v7/convert?q=' +
        query + '&compact=ultra&apiKey=' + apiKey

      this.axios.get(url)
        .then(response => {
          console.log(response)

          // var total = val * amount
          // cb(null, Math.round(total * 100) / 100)
        })
        .catch(error => {
          console.log(error)
        })
    },
    getAllCustomers() {
      this.axios.get('clients')
        .then(response => {
          // console.log('clients', response.data.data.client)
          const dt = response.data.data.client
          const data = []
          for (let i = 0; i < dt.length; i++) {
            const userData = {
              customer_name: dt[i].full_name,
              business_name: dt[i].business_name,
              id: dt[i].id,
            }
            data.push(userData)
          }
          this.usersData = data
        })
        .catch(error => {
          console.log(error)
        })
    },
    handleReset() {
      this.form.resetFields()
    },
    validatePhone(rule, value, callback) {
      const form = this.form
      if (value) {
        if (!this.$validate('phone', form.getFieldValue('pn'))) {
          // eslint-disable-next-line standard/no-callback-literal
          callback('Sorry not a valid phone number!')
        }
      } else {
        callback()
      }
      return true
    },
    handleSubmit(e) {
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (this.converted !== false) {
          if (!err) {
            this.$nprogress.start()
            // console.log(values)

            var data = this.usersData
            var query = values.shipping
            // search through the array and get the index array with the passed value
            var index = data.map(function (img) { return img.id }).indexOf(query)


            const requestPayload = {
              shipping_mark: data[index].customer_name,
              client_id: data[index].id,
              phone: values.pn,
              ctn: values.ctn,
              cbn: values.cbm,
              total_usd: values.usd,
              total_naira: values.naira,
              container_no: values.container_number,
            }

            this.axios.post('shipment/store', requestPayload)
              .then((response) => {
                this.$nprogress.done()
                // console.log(response)
                if (response.status === 200) {
                  this.handleReset()
                  this.$notification['success']({
                    message: 'Shipment Data',
                    description: 'Shipment data created',
                  })
                }
              })
              .catch((error) => {
                this.$nprogress.done()
                // console.log(error)
                this.$notification['warning']({
                  message: 'Shipment Data',
                  description: 'An error occurred',
                })
                return error
              })
          }
        } else {
          this.$notification['warning']({
            message: 'Shipment Data',
            description: 'Sorry currency conversion in progress, do wait',
          })
        }
      })
    },
  },
}
</script>

<style lang="scss" module>
  @import "./style.module.scss";
</style>
